import React,  {useState , useEffect} from 'react'
import { CarouselProvider, Slider, Slide, ButtonBack, ButtonNext } from 'pure-react-carousel';
import 'pure-react-carousel/dist/react-carousel.es.css';
import styles from './Caraousel.module.css'
import CaraouselIcon from '../Section2/CaraouselIcon';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faChevronRight , faChevronLeft } from '@fortawesome/free-solid-svg-icons'

const Caraousel = ({image}) => {

    const [visibleSlides, setVisibleSlides] = useState(0);
    

    useEffect(() => {
        let screenWidth = window.innerWidth;
        if(screenWidth < 1025 && screenWidth > 650){
            
            setVisibleSlides(2);
        }
        else if(screenWidth < 650 ){

            setVisibleSlides(1);
        }
        else{
            setVisibleSlides(3);
        }
    }, [window.innerWidth])

    return (
        <div className={styles.sliderDiv}>
          <CarouselProvider
            naturalSlideHeight={100}
            naturalSlideWidth={100}
            totalSlides={5}
            visibleSlides={visibleSlides}
            infinite
            className={styles.CarouselProvider}
          >
            <Slider>
                {image.map((item , index) => {
                    return (
                        <Slide className={styles.slide} index={index}>
                            <img className={styles.img} src={item.img} />
                            <h2 className={styles.h2}>
                                {item.heading}

                            </h2>
                            <p className={styles.p}>
                                {item.desc}
                            </p>
                        </Slide>
                    )
                })}
            {/* <Slide className={styles.slide} index={0}>
                <img className={styles.img} src="https://www.aesop.com/u1nb1km7t5q7/75EPSgwboNoyvde34GMseV/eba60fe79f72aadee80595701e9d8d5a/Aesop-Hair-Classic-Conditioner-500mL-large.png" />
                <h2 className={styles.h2}>
                    lorem20 ipsum

                </h2>
                <p className={styles.p}>
                    Lightly nourishing care 
                </p>
            </Slide> */}
                    
            </Slider>

                    <ButtonBack className={styles.btn}>
                    <FontAwesomeIcon icon={faChevronLeft} className={styles.leftIcon , styles.icon} />
                    </ButtonBack>
                    <ButtonNext className={styles.btn}>
                        
                    <FontAwesomeIcon icon={faChevronRight} className={styles.rightIcon, styles.icon} />
                        
                    </ButtonNext>
        </CarouselProvider>  
        </div>
    )
}

export default Caraousel
