import React from 'react'
import { Container , Row,Col   } from 'react-bootstrap';
import Button from '../Button';
import styles from "./Section1.module.css"

const Section1 = () => {
    const image = "https://www.aesop.com/u1nb1km7t5q7/7mct7IlGuWuHEn5HI3jIZG/fccaadfa68cc15aab0f5579c7a2a607b/Aesop-Live-Assistance-Desktop-2560x1440px-2-.jpg"
    
    const style= {
        color: "black !important",
        border : "1px solid rgba(51,51,51,.5)",
        padding:"15px 14px",
        width:"60%",
        marginRight:"20px",
        cursor:"pointer",
        transition:"all .2s",
        marginTop:"50px"
    }

    const btn = {
        color:"black !important",
        fontWeight: "500"
        
    }


    return (
        <>
            <Container fluid style={{minHeight: "90vh" ,backgroundColor:"#f6f5e8" }}>
                <Row  >
                    <Col xs={12} md={5} className={"p-0" , styles.content }>
                        <div><h2>For your convenience</h2>
                        <p>For personalised advice, you are welcome to arrange a video consultation with a trained Aesop consultant; or to use our live assistance for guidance while browsing online.
                        </p>
                        <Button title={"Discover our Services"} customBtnStyle={style} btn={btn} />
                        </div>
                    </Col>
                    <Col xs={12} md={7} className="p-0" style={{position: "relative",height:"90vh"}}>
                        <div className = {styles.image} style={{backgroundImage:`url('${image}')`}}>
                            
                        </div>
                    </Col>
                    
                </Row>
            </Container>
        </>
    )
}

export default Section1