import React from 'react'
import { Container , Row,Col   } from 'react-bootstrap';
import styles from "./Product.module.css"

const Product = () => {
    let image1 = "https://www.aesop.com/u1nb1km7t5q7/3HBvecjnqDi2VIcKzpPsfi/0f4b5e5e872949fe558e902a285aa577/Aesop_Tame_and_Sculpt_Web_Secondary_Expander_1_Tame_Desktop_3840x1383px.jpg"
    let image2 = "https://www.aesop.com/u1nb1km7t5q7/2ufG9C0ZajCpeFo5jnSZtW/758ca0063a46d4dbc723eb09ed7d4b5d/Aesop_Tame_and_Sculpt_Web_Secondary_Expander_2_Sculpt_Desktop_3840x1383px.jpg"
    return (
        <>
            <Container fluid style={{minHeight: "100vh"}}>
                <Row>
                    <Col xs={12} md={6} className="p-0">
                        <div className = {styles.image} style={{backgroundImage:`url('${image1}')`}}>
                        <h2>Tame Hair Serum</h2>
                        <p>Petitgrain, Bergamot Rind, Patchouli<br />
                        A serum for strands instead of skin</p>

                        <a>Discover now</a>
                        </div>
                    </Col>
                    <Col xs={12} md={6} className="p-0">
                        <div className = {styles.image} style={{backgroundImage:`url('${image2}')`}}>
                            <h2>Sculpt Hair Polish</h2>
                            <p>Lime, Bergamot Rind, Clove Bud<br />
                                A gel that balances strict and supple</p>

                            <a className={styles.link}>Discover now</a>
                        </div>
                    </Col>
                    
                </Row>
            </Container>
        </>
    )
}

export default Product
