import React, {useEffect} from 'react'
import styles from './Navigation.module.css'
import { Navbar , Nav , Card } from 'react-bootstrap';

const Navigation = () => {
    const btnClicked = (e) => {
        document.getElementById("nav_btn").classList.toggle(styles.nav_btn)
    }

    useEffect(() => {
        let prevScrollpos = window.pageYOffset + 60 ;
        let navbar = document.querySelector("nav");
        let sticky = navbar.offsetTop;
        window.onscroll = function() {
        let currentScrollPos = window.pageYOffset;

        // if (prevScrollpos < currentScrollPos && sticky <= currentScrollPos ) {
            // } else {
                //     document.querySelector("nav").classList.remove("nav-scroll")
                //     // document.getElementById("nav-scroll").style.top = "-50px";
                // }
                // console.log(currentScrollPos,  "neusbu")
                if(currentScrollPos >= sticky  + 60 ){
                    document.querySelector("nav").classList.add("nav-scroll")
                    
                    // document.querySelector("nav").style.top = "60px";
                }
                else{
                    // document.querySelector("nav").style.top = "0px";
                    document.querySelector("nav").classList.remove("nav-scroll")

        }
        prevScrollpos = currentScrollPos;
        }
    }, [])


    return (
        <nav className={styles.nav}>
            
            <Navbar className={styles.navbar} id="nav_btn" expand="lg">
           
            <Navbar.Toggle onClick={btnClicked} aria-controls="basic-navbar-nav" />
            <Navbar.Collapse id="basic-navbar-nav" className="ml-auto">
                <Nav className={"mr-auto" , styles.parentLink} >
                    <Nav.Link className={styles.link} href="#home">Shop</Nav.Link>
                    <Nav.Link className={styles.link} href="#link">Read</Nav.Link>
                    <Nav.Link className={styles.link} href="#home">Stories</Nav.Link>
                    <Nav.Link className={styles.link} href="#link">Search</Nav.Link>
                </Nav>
                <Nav className="ml-auto">
                    <Nav.Link className={styles.link} href="#home">Login</Nav.Link>
                    <Nav.Link className={styles.link} href="#link">Cart</Nav.Link>
                </Nav>
                
            </Navbar.Collapse>
            </Navbar>
        </nav>
    )
}

export default Navigation
