import React from 'react'
import styles from "./Footer.module.css"

const List = ({heading , list}) => {
    return (
        <div className={styles.list}>
            <h1 className={styles.heading}>{heading}</h1>
            <ul className={styles.ul}>
                {list.map(item => {
                    return (
                        <li className={styles.li}>{item}</li>
                    )
                })}
            </ul>
        </div>
    )
}

export default List
