import React from 'react'
import Copywrite from './Copywrite'
import { Container , Row,Col   } from 'react-bootstrap';
import styles from "./Footer.module.css"
import List from './List';


const Footer = () => {
    return (
        <div>
                <Container fluid style={{padding:"30px", backgroundColor:"#252525"}}>
                    <Row>
                        <Col className={styles.column} sm={12} md={4}>
                            <input type={"text"} id="email" className={styles.email} placeholder="Email Address" />
                            <label className={styles.notificationLabel} for="notification"><input name="notification" type={"checkbox"} id="notification" className={styles.notification} />
                                Subscribe to receive communications from Aesop about our products and services. By subscribing, you confirm you have read and accept our<span style={{textDecoration:"underline"}}> privacy policy.</span>
                            </label>
                        </Col>
                        <Col className={styles.column} sm={6} md={3}>
                            <List heading={"Orders and support"} list={["Contact us " , "FAQs" , "Delivery and returns " , "Terms and conditions"]} />     
                        </Col>
                        <Col className={styles.column} sm={6} md={2}>

                            <List heading={"Services"} list={["Live assistance" , "Corporate gifts" , "Facial appointments" ]} />     
                        </Col>
                        <Col className={styles.column} sm={6} md={3}>
                            <List heading={"Location preferences"} list={["Shipping: Hong Kong (S.A.R)" , "Language: English" ]} />     
                            
                        </Col>
                    </Row>
                    <Row className="mt-4">
                    <Col className={styles.column} sm={6} md={4}>
                        
                            <List heading={"Sustainability"} list={["All Aesop products are vegan, and we do not test our formulations or ingredients on animals. We are Leaping Bunny approved and a Certified B Corporation. Learn more" ]} />     
                    </Col>
                        <Col className={styles.column} sm={6} md={3}>
                            
                            <List heading={"About"} list={["Our Story" , "Foundation" , "Careers" ,"Privacy Policy" , "Accessibility"]} />     
                        </Col>
                        <Col className={styles.column} sm={6} md={3}>
                            
                            <List heading={"Social Media"} list={["Instagram" , "Facebook" , "WeCHat" , "LinkedIn" ,"Weibo" ,"" ]} />     
                        </Col>
                    </Row>
                    </Container>


            <Copywrite />

        </div>
    )
}

export default Footer
