import React from 'react'
import '../index.css'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faArrowRight } from '@fortawesome/free-solid-svg-icons'

const Button = (props) => {
    return (
        <div style={props.customBtnStyle} className={"btnHover"}>
            <div style={{color:`${props.color} !important` }} href="/hk/en/r/aesop-raeburn-adventurer-roll-up/" target="_self"><span style={props.btn}>{props.title}</span> 
            <FontAwesomeIcon icon={faArrowRight} className="arrow" /></div>
        </div>
    )
}

export default Button
