import React,{useState} from 'react'
import { Container , Row,Col ,Carousel  } from 'react-bootstrap';
import Button from '../Button';
import CaraouselIcon from './CaraouselIcon';
import styles from "./Section2.module.css"

const Section2 = () => {
    const image1 = "https://www.aesop.com/u1nb1km7t5q7/40xVxq4tBdXpyJLXVTQPvO/aa8ef7a4cca7a713e12b58f1ab8cc6ea/Aesop-HK-K11-Hero-Bleed-Desktop-2880x1620px.jpg"
    const image2 = "https://www.aesop.com/u1nb1km7t5q7/3b6KRiDt3QpNut8LoYGgB9/1d62a7373daa8a6ba4dff6ea300b891e/Aesop-HK-Hollywood-Road-Hero-Desktop-2880x1620.jpg"
    const image3 = "https://www.aesop.com/u1nb1km7t5q7/DqJCGx3tPeDqODieHw5uA/48f50fbd1758ee27990a600c131bd2ce/Aesop-HK-New-Town-Plaza-Hero-Bleed-Desktop-2880x1620px.jpg"
    
    const style= {
        color: "black !important",
        border : "1px solid rgba(51,51,51,.5)",
        padding:"15px 14px",
        minWidth:"60%",
        marginRight:"20px",
        cursor:"pointer",
        transition:"all .2s",
        marginTop:"50px",
        maxWidt:"500px",
    }

    const btn = {
        color:"black !important",
        fontWeight: "500"
        
    }

    const [index, setIndex] = useState(0);

    const handleSelect = (selectedIndex, e) => {
      setIndex(selectedIndex);
    };


    return (
        <>
            <Container fluid style={{minHeight: "90vh" ,backgroundColor:"#f6f5e8" }}>
                <Row  >
                    <Col xs={12} md={5} className={"p-0" , styles.content}>
                        <div><h2>Store Locator</h2>
                        <p>Our consultants are available to host you in-store and provide tailored guidance on gift purchases. 
                        </p>
                        <Button title={"Find a nearby store"} customBtnStyle={style} btn={btn} />
                        </div>
                    </Col>
                    <Col xs={12} md={7} className={"p-0",styles.second} style={{position: "relative"}}>
                        <div className = {styles.image} >
                        <Carousel activeIndex={index} onSelect={handleSelect} interval={3000}
                        nextIcon={<CaraouselIcon direction={"Right"} />}
                        prevIcon={<CaraouselIcon direction={"Left"} />}
                        className="section2-caraousel"
                        >
                        <Carousel.Item className="overlay">
                            <img
                            className="d-block w-100"
                            src={image1}
                            alt="First slide"
                            />
                            <Carousel.Caption>
                            
                            <p>Aesop K11 Musea</p>
                            </Carousel.Caption>
                        </Carousel.Item>
                        <Carousel.Item className="overlay">
                            <img
                            className="d-block w-100"
                            src={image2}
                            alt="Second slide"
                            />

                            <Carousel.Caption>
                            
                            <p>Aesop Hollywood Road</p>
                            </Carousel.Caption>
                        </Carousel.Item>
                        <Carousel.Item className="overlay">
                            <img
                            className="d-block w-100"
                            src={image3}
                            alt="Third slide"
                            />

                            <Carousel.Caption>
                           
                            <p>
                               
                                Aesop New Town Plaza
                            </p>
                            
                            </Carousel.Caption>
                        </Carousel.Item>
                        </Carousel>
                        </div>
                    </Col>
                    
                </Row>
            </Container>
        </>
    )
}

export default Section2