import React from 'react'

const Caption = () => {
    return (
        <section className={"caption"} style={{display:'flex' , justifyContent:"center" , alignItems:"center" , flexDirection:"column", height:"70vh", backgroundColor:"#f6f5e8" , color:"#333",textAlign:"center"
        }}>
            <blockquote style={{fontFamily:"Playfair Display" , fontSize:"30px" , color:"#333" , marginBottom:"60px"}}>
            ‘As for me, I am tormented with an everlasting itch for things remote.’
            </blockquote>
            <p style={{fontSize:"15px" , fontWeight:"600"}}>
                Herman Melville
            </p>
        </section>
    )
}

export default Caption
