import React from 'react'

const Promotion = () => {



    return (
        <div className="promo" style={{backgroundColor: '#252525',  color: '#fffef2'  , width:"100%" , textAlign:'center' ,fontSize:'14px'}}>
           <div  style={{padding:"10px 0px" , display:'flex' , justifyContent:'center' , alignItems:"center"}}> Enjoy complimentary standard shipping on all orders. <span style={{fontSize:"24px",marginLeft : "10px" , position: 'relative' , top:"2px"}}> + </span> </div>
        </div>
    )
}

export default Promotion
