import React from 'react'
import Button from '../Button'
import content from './Content.module.css'

const Content = () => {

    const style= {
        color: "black !important",
        border : "1px solid rgba(51,51,51,.5)",
        padding:"15px 14px",
        width:"70%",
        marginRight:"20px",
        cursor:"pointer",
        transition:"all .2s"
    }

    const btn = {
        color:"black !important",
        fontWeight: "500"
        
    }

    return (
        <>
           <div className={content.content} >
               <header className={content.heading} >
                       <h2 >Aesop and RÆBURN</h2>
                   <h1 >
                       Convenient storage for on-the-go&nbsp;care&nbsp;</h1>
                </header>
                       <div >
                           <div >
                               <p className={content.para} >
                                   The Adventurer Roll Up—our first collaboration with responsible fashion studio RÆBURN—offers a compact way to transport hand care essentials, making it easier to navigate the uncharted territory of now.
                                   </p>
                                   </div>
                                  <Button title={"Discover "} customBtnStyle={style} btn={btn} />
                                      </div>
            </div> 
        </>
    )
}

export default Content
